body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.hero {
    position: relative;
    width: 100%;
    height: 100vh;
}

.hero__image{
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.hero__title {
    position: absolute;
    bottom: 15%;
    right: 10%;
    color: white;
    font-size: 4rem;
    padding: 15px;
}
.slider {
    display: flex;
    align-items: center;
    margin: 20px;

    opacity: 0;
    -webkit-transform: scale(85%);
            transform: scale(85%);
    transition: 2s;
}
.slider--zoom {
    opacity: 1;
    -webkit-transform: scale(100%);
            transform: scale(100%);
    transition: 2s;
}

.slider__image {
    height: 50rem;
    width: 50rem;
    object-fit: cover;
}

.slider__content {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    text-align: center;
}
.slider__title {
    text-align: center;
}

@media screen and (max-width: 800px) {
    .slider {
        flex-direction: column;
        background-color: rgb(236, 236, 236);
    }

    .slider__image {
        width: 100%;
    }
    
}
.navbar {
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    height: 70px;
    background-color: black;
    opacity: 0.75;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 30px 0px 30px;
    margin-bottom: -70px;
    z-index: 5;
}

.navbar__logo {
    color: white;
    font-family: 'Quicksand', sans-serif;
    font-size: 2.5rem;
}
.navbar__list {
    display: flex;
    list-style: none;
}
.navbar_item {
    white-space: nowrap;
}
.navbar__link {
    font-size: 0.9rem;
    text-decoration: none;
    color: white;
    text-align: center;
    padding: 10px;
    margin: 0px 5px;
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    border-color: transparent;
    transition: 250ms;
}
.navbar__link:hover {
    border-color: white;
    transition: 250ms;
}
.navbar__menu {
    cursor: pointer;
    color: white;
    display: none;
}

@media screen and (max-width:800px) {

    .navbar__list {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        position: absolute;
        top: 70px;
        left: -100%;
        width: 100%;
        height: 300px;
        transition: all 0.5s;
    }

    .navbar__list--active {
        left: 0;
    }

    .navbar_item{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 0;
        flex: 1 1;
    }

    .navbar__link{
        display: flex;
        width: 100px;
        height: 100px;
        justify-content: center;
        align-items: center;
        margin: 0px;
        border-width: 0px;
        background-color: black;
        opacity: 0.75;
    }

    .navbar__link:hover {
        opacity: 0.9;

    }
    .navbar__menu{
        display: block;
    }
    
}
